  /**
      * Get the data of the users and if the guid user is Anonymized the request is don't to all else guids
      * @param params
      * @returns {Array} with users information
      */
  getUsersData: function getUsersData(params) {
    var self = this,
    defer = new $.Deferred();
    params.userIds = params.usersGuids ? params.usersGuids : params.userIds;
    var allguids = params.userIds.toString().split(",");
    var guidsToSend = allguids.filter(function (guid) {
      return guid != bizagi.util.anonymizedUserInformation.id;
    });
    var hasAnnonimizedUser = allguids.length != guidsToSend.length;
    if (guidsToSend.length === 0 && hasAnnonimizedUser) {
      defer.resolve([bizagi.util.anonymizedUserInformation]);
    } else {
      params.userIds = guidsToSend.join(",");
      params.usersGuids = params.userIds;
      $.post(self.serviceLocator.getUrl("project-users-get"), params || {}).done(function (response) {
        if (hasAnnonimizedUser)
        response.push(bizagi.util.anonymizedUserInformation);
        defer.resolve(response);
      }).fail(function (params) {
        defer.reject(params);
      });
    }
    return defer;
  },

