  /**
      * Logout Service
      */
  logout: function logout(overridelogoutUrl) {
    var self = this;
    var logoutData = { sourceLogout: "user" };
    // Remove session storage of authentication data
    sessionStorage.removeItem("bizagiAuthentication");

    //Call to the restfull service for domain list
    try {
      $.when($.read(bizagi.proxyPrefix + "Rest/Users/CurrentUserGuid")).done(function (currentUser) {
        var domain = { domain: currentUser.domain };
        $.when(self.getConfiguration(domain.domain)).done(function (data) {
          if (data.redirectErrorPage) {
            window.location.href = data.redirectErrorPage;
            return;
          }

          var authenticationType = data.authenticationType;
          var authenticationSubType = data.authenticationSubType;
          switch (authenticationType) {
            case 'Federate':
              self.federateAuthenticatorLogOff(data, authenticationSubType, logoutData, overridelogoutUrl);
              break;
            case 'SAML':
              loader.nativeAjax(loader.getPathUrl(self.serviceLocator.getUrl("logout"), logoutData), function (response) {
                var jsonResponse = JSON.parse(response.responseText);

                if (jsonResponse.logout === true) {
                  if (typeof jsonResponse.samlUrlRedirect != 'undefined') {
                    var url = decodeURI(jsonResponse.samlUrlRedirect);
                    var iframe = document.createElement("iframe");
                    document.getElementsByTagName("body")[0].appendChild(iframe);
                    $(iframe).on("load", function () {
                      var iframeContent = bizagi.util.isIE8() ? $(this.contentWindow.document.body) : $(this.contentWindow.eval("document.body"));
                      var rawResponse = iframeContent.text();
                      location.reload();
                    });

                    iframe.src = url;
                  } else {
                    location.reload();
                  }
                } else {
                  bizagi.log("Error logging out");
                }
              });
              break;
            case 'OAuth2':
              self.oauth2AuthenticatorLogOff(logoutData, overridelogoutUrl);
              break;
            case 'Multiple':
              $.when($.read(self.serviceLocator.getUrl("multipleAuthenticatorConfig"), domain)).done(function (multipleAuthConfig) {
                switch (multipleAuthConfig.multipleAuthenticationType.toLowerCase()) {
                  case 'federated':
                    self.federateAuthenticatorLogOff(multipleAuthConfig, multipleAuthConfig.multipleAuthenticationSubType, logoutData, overridelogoutUrl);
                    break;
                  case 'oauth2':
                    self.oauth2AuthenticatorLogOff(logoutData, overridelogoutUrl, domain);
                    break;
                  default:
                    self.authenticatorLogOff(logoutData, overridelogoutUrl);
                    break;}

              }).fail(function (message) {
                console.log(message);
                self.authenticatorLogOff(logoutData, overridelogoutUrl);
              });
              break;
            default:
              self.authenticatorLogOff(logoutData, overridelogoutUrl);
              break;}

        }).fail(function (message) {
          console.log(message);
        });
      }).fail(function (err) {
        console.log(err);
      });
    } catch (e) {
      console.log(e);
    }
  },
  federateAuthenticatorLogOff: function federateAuthenticatorLogOff(data, authenticationSubType, logoutData, overridelogoutUrl) {
    var self = this;
    var logOffURL = data.logOffURL;
    var federationLogoutBindingProtocol = data.federationLogoutBindingProtocol;

    if (authenticationSubType) {
      authenticationSubType = authenticationSubType.toLowerCase();
    }

    if (authenticationSubType === 'wsfederated') {
      $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
        if (overridelogoutUrl && overridelogoutUrl != "") {
          logOffURL = logOffURL.replace(/wreply=[^&]*/, "wreply=" + overridelogoutUrl);
        }

        location.href = logOffURL;
      }).fail(function () {
        bizagi.log("Error logging out");
      }).always(function () {
        location.href = data.logOffURL;
      });
    } else
    if (authenticationSubType === 'saml') {
      var param = {};
      if (sessionStorage.getItem("userIdentity")) {
        param = { "userIdentity": sessionStorage.getItem("userIdentity") };
      }
      $.when($.read(self.serviceLocator.getUrl("FederateLogoffUrl"), param)).done(function (res) {
        if (res.federateLogoffUrl) {
          logOffURL = res.federateLogoffUrl;
        }

        $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
          if (federationLogoutBindingProtocol === 'POST') {
            window.document.open('text/html');
            window.document.write(logOffURL);
            window.document.close();
          } else
          {
            window.location.href = logOffURL;
          }
        }).fail(function () {
          bizagi.log("Error logging out");
        }).always(function () {
        });

      }).fail(function () {
        bizagi.log("Error logging out");
      }).always(function () {
      });
    }
  },
  oauth2AuthenticatorLogOff: function oauth2AuthenticatorLogOff(logoutData, overridelogoutUrl, domain) {
    var self = this;

    $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function (logoutResponse) {
      $.when($.read(self.serviceLocator.getUrl("oauth2AuthenticationConfig"), domain)).done(function (response) {
        if (response.logoutUrl && response.logoutUrl != "") {
          var logoutUrl = response.logoutUrl;
          if (overridelogoutUrl) {
            if (!overridelogoutUrl.endsWith('/')) {
              overridelogoutUrl += '/';
            }

            logoutUrl = logoutUrl.replace(/post_logout_redirect_uri=[^&]*/, "post_logout_redirect_uri=" + overridelogoutUrl);
          }

          if (logoutResponse.invalidateAccessToken) {
            logoutUrl = logoutUrl.replace(/invalidateAccessToken=[^&]*/, "invalidateAccessToken=" + logoutResponse.invalidateAccessToken);
          }

          if (logoutResponse.idTokenHint && logoutUrl.indexOf("id_token_hint") >= 0) {
            logoutUrl = logoutUrl + logoutResponse.idTokenHint;
          }

          location.href = logoutUrl;
        } else
        {
          location.reload();
        }
      }).fail(function () {
        bizagi.log("Error logging out");
      });
    }).fail(function () {
      bizagi.log("Error logging out");
    });
  },
  authenticatorLogOff: function authenticatorLogOff(logoutData, overridelogoutUrl) {
    var self = this;

    $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
      if (overridelogoutUrl) {
        location.href = overridelogoutUrl;
      } else
      {
        window.location = bizagi.services.ajax.loginPage;
      }
    }).fail(function () {
      bizagi.log("Error logging out");
    });
  },
