    if (params["dataType"] == "domains") {
      url = "admin-getAuthenticationDomains";
    } else if (params["dataType"] == "events") {
      url = "admin-getAuthenticationEventsTypes";
    } else {
      url = "admin-getAuthenticationEventSubTypes";
    }

    // Call ajax and returns promise
